@import url("https://fonts.googleapis.com/css2?family=Shadows+Into+Light");

:root {
  --bg-color: #00c0c8;
  --font-color: #ffffff;
  --btn-bg-color: #ffffff;
  --accent-color: #ff000d;
}

.App {
  text-align: center;
  font-family: 'Shadows Into Light', cursive;
  color: rgb(0, 0, 0);
  font-size: 32px;

}

* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
  background-image: url("./images/background.png");
  background-size: cover;
  background-repeat: repeat;
}

.overlay {
  opacity: 0.85;
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: fixed;
}

.background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("./images/background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 40% 40%;
}

.btn {
  display: inline-block;
  padding: 1rem 2rem;
  background: var(--btn-bg-color);
  color: var(--accent-color);
  font-family: var(--font-secondary);
  font-style: bold;
  font-size: 1rem;
  text-transform: capitalize;
  transition: all 0.3s ease;
  box-shadow: 4px 4px 0 0 var(--accent-color);
  cursor: pointer;
  position: relative;
  text-decoration: none;
  outline: none;

}

.btn:hover {
  box-shadow: none;
}

.mint {
  display: inline-block;
  padding: 1rem 2rem;
  background: var(--btn-bg-color);
  color: var(--accent-color);
  font-family: var(--font-secondary);
  font-style: italic;
  font-size: 20px;
  text-transform: capitalize;
  transition: all 0.3s ease;
  box-shadow: 4px 4px 0 0 var(--accent-color);
  cursor: pointer;
  position: relative;
  text-decoration: none;
  outline: none;
  border: none;
  text-align: center;
}